.why__header {
	margin-bottom: 1.25rem;
}

.why__item {
	margin-top: 1.25rem;
	width: 100%;
}

.why__item:first-child {
	margin-bottom: 0;
}

@media (min-width: 48rem) {
	.why__header {
		margin-bottom: 2rem;
	}

	.why__content {
		display: flex;
		flex-wrap: wrap;
	}

	.why__item {
		flex-basis: calc(50% - 16px);
		margin-right: 16px;
		margin-top: 2rem;
	}

	.why__item:first-child,
	.why__item:nth-child(2) {
		margin-top: 0;
	}

	.why__item > *:only-child {
		height: 100%;
	}
}

@media (min-width: 48rem) and (max-width: 63.9999375rem) {
	.why__item:nth-child(even) {
		margin-right: 0;
	}
}

@media (min-width: 64rem) {
	.why__item {
		flex-basis: calc(33.3333% - 16px);
		margin-right: 24px;
	}

	.why__item:nth-child(3n+3) {
		margin-right: 0;
	}

	.why__item:nth-child(3) {
		margin-top: 0;
	}
}
